export class Color {
    constructor() {
        this.r = 0;
        this.g = 0;
        this.b = 0;
        this.a = 1;
    }

    set(r, g, b, a) {
        this.r = r;
        this.g = g;
        this.b = b;
        this.a = a;
    }
}


